* {
    font-family: 'IBM Plex Sans';
    background-color: #f6f6f6;
}


.visually-hidden {
    position: absolute;
    overflow: hidden;
    margin: -1px;
    border: 0;
    padding: 0;
    width: 1px;
    height: 1px;
    white-space: nowrap;
    clip-path: polygon(0px 0px, 0px 0px,0px 0px, 0px 0px);
}
.page__catalog {
    margin-top: 32px;
}
.catalog {
    display: grid;
    grid-template-columns: repeat(auto-fill, 450px);
    column-gap: 34px;
    row-gap: 48px;
    justify-content: center;
}
.scale {
    display: inline-block;
}
.scale img {
    -webkit-transition: 0.5s;
    transition: 0.5s;
    display: block;
}
.scale img:hover {
    -webkit-transform: -webkit-scale(1.05);
	transform: scale(1.05);
}
.bottom {
    position:relative;
    bottom: -3px;
    left: 0px;
}
.card__description {
  padding: 12px 0px;
  box-sizing: border-box;
}
.card__title {
  margin: 0;
  font-weight: bold;
  font-size: 18px;
  line-height: 20px;
  padding: 12px 5px 0;
}
.card__url {
    display: block;
    color: #3C39EC;
    font-weight: 500;
    font-size: 16px;
    line-height: 1.5;
    text-decoration: none;
    padding: 8px 5px 0;
}
.card__info {
    font-size: 14px;
    color: #0C0C0D;
    margin: 8px 0 0;
    -webkit-opacity: 0.6;
    opacity: 0.6

}
.card__info{display: inline;}

.card_img {
    width: 100%;
    height: 100%;
}
.header__block {
    display: flex;
    justify-content: space-around;
    align-items: center;
    min-height: 96px;
    z-index: 9;
    position: relative;
    justify-content: center;
    margin-bottom: 24px;
}
.header__row {
    display: flex;
    align-items: center;
}
.header__logo {
  margin-right: 8px;
  width: 32px;
}
.header__title {
    font-size: 20px;
    line-height: 1.6;
    margin: 0;
    text-shadow: 4px -2px 3px #11111145;
}
.header__epigraph {
    text-shadow: 4px -2px 3px #11111145;
}
.page {
    margin: 0;
    box-sizing: border-box;
    padding: 0 24px;
}
.card__description a {
    word-wrap: break-word;
}
.next-left {
    float: left;
}
.next-right {
    float: right;
}
.selector,
.selector-simple {
    position: relative;
    top: 0;
    left: 0;

    border: 1px solid rgba(114, 113, 113, 0.62);
    border-radius: 8px;
    -webkit-box-shadow: 5px 5px 30px rgb(0 0 0 / 50%);
    box-shadow: 5px 5px 30px rgb(0 0 0 / 50%);
    -webkit-transition: -webkit-transform 1.75s cubic-bezier(.075, .5, 0, 1), -webkit-box-shadow .75s ease-out;
    transition: transform 1.75s cubic-bezier(.075, .5, 0, 1), box-shadow .75s ease-out;
    will-change: -webkit-transition;
    will-change: transition;
}
.selector:hover  {
    -webkit-box-shadow: 5px 5px 30px rgb(243 243 129/ 70%);
    box-shadow: 5px 5px 30px rgb(243 243 129/ 70%);
}
.card-items {
	position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    left: 50%;
	top: 35vh;
	z-index: 1;
}
.background-grey {
	position: fixed;
	background-color:#0c0c0d5e;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	z-index: 1;
}
.background-white {
	position: absolute;
	background-color: #f6f6f6;
	display: flex;
	padding: 20px;
	gap:20px;
	border-radius: 16px;
	width: auto;
	height: auto;
	z-index: 1;
}
.text-break-word {
    word-wrap: break-word;
    -webkit-text-size-adjust: 100%;
}
.zoomIn {
	-webkit-animation: zoomIn 1s 1;
	animation: zoomIn 1s 1;
    -webkit-opacity: 1;
	opacity: 1;
}
.card-item {
	width: 300px;
	height: auto;
}
@keyframes zoomIn {
	from {
        -webkit-opacity: 0;
        opacity: 0;
		-webkit-transform: -webkit-scale3d(.3, .3, .3);
		transform: scale3d(.3, .3, .3);
	}
	50% {
        -webkit-opacity: 1;
		opacity: 1;
	}
}
@media  (max-width: 720px)  {

    .catalog {
        display: grid;
        grid-template-columns: repeat(1, 1fr);
        grid-template-columns: repeat(1, 350px);
        column-gap: 24px;
        row-gap: 48px;
        justify-content: center;
        justify-items: center;

        display: block;
        margin: 0 auto;
    }
    .page {
        padding: 0;
    }
    .catalog__item {
        margin: 0 5vw;
        padding-bottom: 12vw;
    }

}
@media (max-width: 479px) {
    .header__picture {
        margin-bottom: 10px;
    }
    .header__block {
        flex-direction: column;
        margin-bottom: 24px;
    }

    .header__title {
        font-size: 1em;
        word-wrap: break-word;
    }

    .card__title {
        font-size: 14px;
    }
    .card__ur {
        line-height: 1.2em;
        font-size: 12px;
    }
    .card__img {
        width: 300px;
        height: 200px;
    }

    .header__img {
        width: auto;
        height: 100%;
    }
}
@media (max-width: 260px) {
    .catalog {
        grid-template-columns: repeat(1, 1fr);
    }
}
